'use client';

import { useCallback, useEffect, useState } from 'react';
import { useClient } from '@/contexts/ClientContext';
import { useThread } from '@/contexts/ThreadContext';
import { useChat } from '@/contexts/ChatContext';

interface UseChatActionsProps {
  threadId?: string;
  assistantId?: string;
}

export function useChatActions({ threadId: initialThreadId, assistantId }: UseChatActionsProps = {}) {
  const client = useClient();
  const { loadThreadHistory } = useThread();
  const { setIsLoading } = useChat();
  const [threadId, setThreadId] = useState<string | undefined>(initialThreadId);
  const [ready, setReady] = useState(false);

  useEffect(() => {
    async function init() {
      if (!client || !assistantId) return;

      try {
        // If no thread ID is provided, create a new one
        if (!threadId) {
          const thread = await client.threads.create({
            metadata: {
              assistant_id: assistantId
            }
          });
          setThreadId(thread.thread_id);
        }

        // Load thread history
        if (threadId) {
          await loadThreadHistory(threadId);
        }

        setReady(true);
      } catch (error) {
        console.error('Error initializing chat:', error);
        setReady(false);
      }
    }

    init();
  }, [client, threadId, assistantId, loadThreadHistory]);

  const sendMessage = useCallback(async (content: string) => {
    if (!client || !threadId || !assistantId) {
      throw new Error('Chat not initialized or missing assistant ID');
    }

    setIsLoading(true);
    try {
      const response = await fetch('/api/messages', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          thread_id: threadId,
          assistant_id: assistantId,
          message: content,
        }),
      });

      if (!response.ok) {
        throw new Error('Failed to send message');
      }

      // Load updated thread history after sending message
      await loadThreadHistory(threadId);
    } catch (error) {
      console.error('Error sending message:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  }, [client, threadId, assistantId, loadThreadHistory, setIsLoading]);

  return {
    threadId,
    ready,
    sendMessage,
  };
} 