'use client';

import { useState } from 'react';
import { Send } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import { motion } from 'framer-motion';

interface ChatInputProps {
  onSendMessage: (content: string) => Promise<void>;
  isLoading: boolean;
  isReady?: boolean;
}

export function ChatInput({ onSendMessage, isLoading, isReady = true }: ChatInputProps) {
  const [input, setInput] = useState('');

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!input.trim() || !isReady) return;

    try {
      await onSendMessage(input.trim());
      setInput('');
    } catch (error) {
      console.error('Error sending message:', error);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="max-w-3xl mx-auto p-4">
      <div className="flex gap-4 items-center">
        <div className="relative flex-1">
          <input
            type="text"
            value={input}
            onChange={(e) => setInput(e.target.value)}
            placeholder={isReady ? "Type a message..." : "Select or create a chat to start"}
            className={cn(
              "w-full bg-[#262626]/50 text-gray-200 rounded-lg pl-4 pr-10 py-3",
              "focus:outline-none focus:ring-2 focus:ring-blue-500/50",
              "placeholder-gray-500",
              (!isReady || isLoading) && "opacity-50 cursor-not-allowed"
            )}
            disabled={!isReady || isLoading}
          />
          {input.trim() && (
            <div className="absolute right-3 top-1/2 -translate-y-1/2">
              <kbd className="hidden sm:inline-block px-2 py-1 text-xs font-medium text-gray-500 bg-[#1A1A1A] rounded">⌘ ↵</kbd>
            </div>
          )}
        </div>
        <motion.div whileHover={{ scale: 1.05 }} whileTap={{ scale: 0.95 }}>
          <Button 
            type="submit" 
            disabled={!isReady || isLoading || !input.trim()}
            className={cn(
              "bg-blue-500 hover:bg-blue-600 text-white shadow-lg transition-all",
              (!isReady || isLoading) && "opacity-50 cursor-not-allowed"
            )}
          >
            <Send className="w-4 h-4" />
          </Button>
        </motion.div>
      </div>
    </form>
  );
} 