'use client';

import { MessageSquare, Plus, Trash2 } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { cn } from '@/lib/utils';
import { useThread } from '@/contexts/ThreadContext';
import { useClient } from '@/contexts/ClientContext';
import { useState } from 'react';
import { Thread } from '@/types/chat';
import { motion } from 'framer-motion';

interface ChatThreadProps {
  threads: Thread[];
  onNewChat: () => void;
  onSelectThread: (threadId: string) => void;
  activeThreadId?: string;
}

const listVariants = {
  hidden: { opacity: 0 },
  show: {
    opacity: 1,
    transition: {
      staggerChildren: 0.1
    }
  }
};

export function ChatThread({ threads, onNewChat, onSelectThread, activeThreadId }: ChatThreadProps) {
  const { deleteThread } = useThread();
  const client = useClient();
  const [isCreating, setIsCreating] = useState(false);

  const handleNewChat = async () => {
    if (!client || isCreating) return;
    
    setIsCreating(true);
    try {
      await onNewChat();
    } catch (error) {
      console.error('Error creating new thread:', error);
    } finally {
      setIsCreating(false);
    }
  };

  const handleDeleteThread = async (e: React.MouseEvent, threadId: string) => {
    e.stopPropagation();
    try {
      await deleteThread(threadId);
    } catch (error) {
      console.error('Error deleting thread:', error);
    }
  };

  const handleThreadSelect = (threadId: string) => {
    if (threadId === activeThreadId) return;
    onSelectThread(threadId);
  };

  return (
    <div className="h-full w-80 flex flex-col bg-gradient-to-b from-[#141414] to-[#1A1A1A] border-r border-[#262626]/50">
      {/* Header */}
      <div className="flex-none p-4 border-b border-[#262626]/50">
        <motion.button 
          whileHover={{ scale: 1.02 }}
          whileTap={{ scale: 0.98 }}
          onClick={handleNewChat}
          className="w-full px-4 py-2.5 bg-blue-500 hover:bg-blue-600 text-white rounded-lg transition-colors flex items-center gap-2 shadow-lg shadow-blue-500/20"
        >
          <Plus className="w-4 h-4" />
          New Chat
        </motion.button>
      </div>
      
      {/* Scrollable threads list */}
      <div className="flex-1 overflow-y-auto scrollbar-thin scrollbar-thumb-[#262626] scrollbar-track-transparent">
        {threads.length === 0 ? (
          <div className="p-8 text-center text-gray-500 text-sm flex flex-col items-center gap-3">
            <MessageSquare className="w-8 h-8 text-gray-600" />
            <p>No chats yet</p>
            <p className="text-xs text-gray-600">Start a new conversation to begin</p>
          </div>
        ) : (
          <motion.div
            variants={listVariants}
            initial="hidden"
            animate="show"
            className="py-2"
          >
            {threads.map((thread) => (
              <div
                key={`thread-${thread.thread_id}`}
                className={cn(
                  "group relative w-full p-3 text-left transition-colors flex items-start gap-3",
                  "hover:bg-[#262626]",
                  activeThreadId === thread.thread_id ? "bg-[#262626]" : "bg-transparent"
                )}
              >
                <button
                  onClick={() => handleThreadSelect(thread.thread_id)}
                  className="flex items-start gap-3 flex-1"
                >
                  <div className="w-5 h-5 mt-1 flex-shrink-0">
                    <MessageSquare className="w-full h-full text-gray-400" />
                  </div>
                  <div className="flex-1 min-w-0 text-sm">
                    <p className="text-gray-200 truncate">
                      {new Date(thread.created_at).toLocaleString()}
                    </p>
                    <p className="text-gray-500 text-xs truncate mt-0.5">
                      {thread.messages[thread.messages.length - 1]?.content || 'No messages yet'}
                    </p>
                  </div>
                </button>
                <button
                  onClick={(e) => handleDeleteThread(e, thread.thread_id)}
                  className="opacity-0 group-hover:opacity-100 transition-opacity p-1 hover:bg-[#333333] rounded"
                  disabled={isCreating}
                >
                  <Trash2 className="w-4 h-4 text-gray-400 hover:text-red-400" />
                </button>
              </div>
            ))}
          </motion.div>
        )}
      </div>
    </div>
  );
} 