'use client';

import { useAssistant } from '@/contexts/AssistantContext';
import { cn } from '@/lib/utils';
import { Bot } from 'lucide-react';

export function AssistantSelector() {
  const { assistants, selectedAssistantId, setSelectedAssistantId, isLoading, error } = useAssistant();

  if (isLoading) {
    return (
      <div className="flex items-center gap-2 px-3 py-2 text-sm text-gray-400">
        <Bot className="w-4 h-4 animate-pulse" />
        <span>Loading assistants...</span>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex items-center gap-2 px-3 py-2 text-sm text-red-400">
        <Bot className="w-4 h-4" />
        <span>Error loading assistants</span>
      </div>
    );
  }

  return (
    <div className="flex items-center gap-2 px-3 py-2">
      <Bot className="w-4 h-4 text-gray-400" />
      <select
        value={selectedAssistantId || ''}
        onChange={(e) => setSelectedAssistantId(e.target.value || null)}
        className={cn(
          "flex-1 bg-transparent text-sm text-gray-200",
          "border-none outline-none focus:ring-0",
          "appearance-none cursor-pointer"
        )}
      >
        <option value="" disabled>Select an assistant</option>
        {assistants.map((assistant) => (
          <option
            key={assistant.assistant_id}
            value={assistant.assistant_id}
            className="bg-[#1A1A1A] text-gray-200"
          >
            {assistant.name || assistant.assistant_id}
          </option>
        ))}
      </select>
    </div>
  );
} 